import interfaces from "../../utils/interfaces.js"

export default {
	data() {
		return {
			goodsList: [], // 商品列表大数组
			filterby: "all", // 默认选择的tab
			page: 1,
			size: 6,
			isLoad : true,
			loadingText: "Show More~",
			filterByList: [{
					text: "全部",
					selected: true,
					filterby: "all"
				},
				{
					text: "口碑",
					selected: false,
					filterby: "public"
				},
				{
					text: "热门",
					selected: false,
					filterby: "hot"
				}
			]
		}
	},
	onLoad(option){
		// console.log(option)
		// 动态修改nav title
		uni.setNavigationBarTitle({
			title: option.name
		})
		
		//加载数据
		this.loadData();
	},
	methods: {
		handleSelect(index){
			//设置选中状态栏
			this.filterByList.forEach((item, fIndex) => {
				fIndex == index ? item.selected = true : item.selected = false;
			});
			
			// 加载数据
			this.filterby = this.filterByList[index].filterby;
			this.page = 1;
			this.isLoad = true;
			this.loadingText = "加载中，请稍后~";
			this.goodsList = [];
			this.loadData();
		},
		loadData(){
			if(this.isLoad){
				this.request({
					url: `${interfaces.getGoodsList}/${this.filterby}/${this.page}/${this.size}`,
					success: (res) => {
						if(res.data.length > 0){
							res.data.forEach(item => {
								this.goodsList.push(item);
							})
						}else{
							this.loadingText = "That's all~";
							this.isLoad = !this.isLoad;
						}
					}
				})
			}
			
		},
		//下拉刷新
		onPullDownRefresh(){
			setTimeout(() => {
				this.page = 1;
				this.loadingText = "加载中，请稍后~";
				this.goodsList = [];
				this.isLoad = true;
				this.loadData();
				uni.stopPullDownRefresh();
			},1000)
		},
		// 上拉加载
		onReachBottom(){
			this.page++;
			this.loadData();
		}
	}
}
